@mixin flexy {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

%font-12 {
  font-size: 12px;
  line-height: 15px;
  color: #000;
  text-decoration: none;
  font-weight: 500;
}

%font-14 {
  font-size: 14px;
  line-height: 16px;
}

%font-16 {
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
}

$blue: #00acdc;
$white: #fff;
$link-padding: 22.5px;
$card-width: 200px;
$logo-size: 40px;
